body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.toDo-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px;
    background-color: #2f2534;
    color: white;
    margin: 4px;
    border-radius: 15px;
}

.toDo-item p{
    font-size: 20px;
}

.toDo-item svg{
    width: 25px;
}
.list-container{
    width: 100%;
    max-height: 80vh;
    overflow-y: scroll;
}

.toDo-input-form{
    display: flex;
    flex-direction: column;
    padding: 4px;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    padding: 4px;
}

.toDo-input-form input{
    padding: 4px;
    text-align: center;
    font-size: 20px;
    background-color: white;
    border: none;
    border-radius: 3px;
    outline: none;
    width: 95%;
}

.toDo-input-form button{
    padding: 0.5rem;
    border: none;
    border-radius: 3px;
    outline: none;
    width: 200px;
    color: white;    
    cursor: pointer;
    background-color: #2f2834;
    margin: auto;
}
